.row::before,
.row::after {
  content: '';
  display: table;
  clear: both; }

.grid-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto; }

.col {
  position: relative;
  float: left;
  min-height: 1px; }

.col-1 {
  width: 6.866666667%; }

.col-2 {
  width: 15.3333333333%; }

.col-3 {
  width: 23.8%; }

.col-4 {
  width: 32.2666666667%; }

.col-5 {
  width: 40.7333333333%; }

.col-6 {
  width: 49.2%; }

.col-7 {
  width: 57.6666666667%; }

.col-8 {
  width: 66.1333333333%; }

.col-9 {
  width: 74.6%; }

.col-10 {
  width: 83.0666666667%; }

.col-11 {
  width: 91.5333333333%; }

.col-12 {
  width: 100%; }

.col + .col {
  margin-left: 1.6%; }

@media (max-width: 550px) {
  [class^="col"] {
    width: auto;
    float: none;
    margin: 1rem 0; }
  .col + .col {
    margin-left: 0; } }
